import React, { Component } from 'react'
import { NavLink, Link } from 'react-router-dom'
import { Button, IconButton, Menu, MenuItem } from 'material-ui'
import AV from 'leancloud-storage'
import md5 from 'blueimp-md5'
import './header.css'
import { Bell } from './svg.js'
import cookie from "react-cookies";
import { browserHistory } from 'react-router'
import $ from "jquery"
import uniqid from "uniqid"
class Header extends React.Component {
  constructor(props, context) {
    super(props)
    let url = "http://localhost:8081/users/avatar/" + cookie.load('userId');

    this.state = { anchorEl: null, menuShow: false, url, numer: 0 ,searchvalue:0,InputValue : ""}
    this._clickHead = this._clickHead.bind(this)
    this._handleClose = this._handleClose.bind(this)
    this._handleMenuClose = this._handleMenuClose.bind(this)
    this._clickLogin = this._clickLogin.bind(this)
    this._clickNotice = this._clickNotice.bind(this)
    this._verify = this._verify.bind(this)
  }
  // Dom 
  componentWillMount() {
  }
  //  Dom 
  componentDidMount() {
  }
  render() {
    return (
      <header className="header">
        <div className="g-container container">
          <div className="left">
            <Link className="logo" to="/">  </Link>
            <nav>
              <Button className="button"><NavLink exact to="/" className="g-color-gray a" activeClassName="selected">  </NavLink></Button>
              <Button className="button"><NavLink to="/college" className="g-color-gray a" activeClassName="selected">  </NavLink></Button>
            </nav>
            <input type="input"
                   id="search1"
                   onChange={this.handleGetInputValue}
                   style={{width:"300px",
                    marginLeft:"60px",
                     height:"24px",
                     borderRadius: "2px",
                     boxShadow: "0 1px 2px 0 rgba(0,0,0,.05)",
                   }} placeholder=""/>
            <button disabled={this.state.searchstate==false}
                style={{marginLeft:"10px",
                  height:"30px",
                  color: "#ffffff",
                  background: "#0f88eb",
                  borderRadius: "2px",
                  border:"none",
                }}> <li style={{listStyle:"none"}}><Link style={{textDecoration:"none",color:"white"}} to={{pathname:"/searchpage/"+this.state.InputValue, hash:"", query:{foo: this.state.InputValue, boo:'boz'}}} activeClassName="GlobalNav-active"></Link></li></button>
          </div>
          {this._userShow()}
        </div>

        <Menu
          open={this.state.menuShow}
          anchorEl={this.state.anchorEl}
          onClick={this._handleMenuClose}
          id="menuList"
        >
          <MenuItem onClick={this._handleClose.bind(this, 0)}></MenuItem>
          <MenuItem onClick={this._handleClose.bind(this, 1)}></MenuItem>
          <MenuItem onClick={this._handleClose.bind(this, 2)}></MenuItem>
          <MenuItem onClick={this._handleClose.bind(this, 3)}></MenuItem>
        </Menu>
      </header>
    )
  }
  _userShow() {
    return cookie.load('userId')!=null?
      (<div className="right">
        <Button className="button buttonw "><NavLink to="/write" className="a" activeClassName="selected">  </NavLink></Button>
        <IconButton className="bell" onClick={this._clickNotice}>
          <span className="tag" style={{ display: this.state.numer ? '' : 'none' }}>{this.state.numer}</span>
          <Bell className="g-color-gray-fill" />
        </IconButton>
        <img className="headimg" onClick={this._clickHead} src={this.state.url} alt="header" />
      </div>) :
      (<div className="right">
        <Button className="button login" onClick={this._clickLogin}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</Button>
      </div>)
  }
  // 
  _clickNotice(e){
    this.props.history.push('/notice')
  }
  // 
  _clickHead(e) {
    const { menuShow } = this.state
    this.setState({
      anchorEl: e.currentTarget,
      menuShow: !menuShow
    })
  }
  handleGetInputValue = (e) => {
    this.setState({
      InputValue : e.target.value
    })
  };
  _verify(e){
    if(this.state.InputValue==null){
      this.setState({searchstate:false})}
      else
        this.setState({searchstate:true})
    }
  // 
  _clickLogin(e) {
    this.props.history.push('./login')
  }
  //  item
  _handleClose(i, e) {
    this.setState({ menuShow: false })
    if (i === 0) {
      this.props.history.push('/me')
      return
    }
    if (i === 1) {
      this.props.history.push('/atricle')
      return
    }
    if (i === 2) {
      this.props.history.push('/userhistory')
      return
    }
    if (i === 3) {
      cookie.remove('userId', { path: '/' })
      this.props.history.push('/')
      return
    }
  }
  // 
  _handleMenuClose() {
    this.setState({ menuShow: false })
  }
  //  Props 
  componentWillReceiveProps(nextProps) {

  }
  //  Props  State 
  shouldComponentUpdate(nextProps, nextState) {
    return true
  }
  // Dom  
  componentWillUpdate(nextProps, nextState) {

  }
  //  Dom 
  componentDidUpdate() {

  }
  // 
  componentWillUnmount() {

  }
}

export default Header



