import React, { Component } from 'react'
import AV from "leancloud-storage"
import AtricleItem from "../component/atricleItem"
import Message from "../component/message"
import Progress from "../component/progress"
import SnackBar from "../component/snackbar"
import Header from "../component/header"

import "./home.css"
import axios from "axios";

class Home extends Component {
  // 
  constructor(props, context) {
    super(props);
    this.state = { items: [] };
  }

  // Dom 
  componentWillMount() {

  }

  //  Dom 
  componentDidMount() {
    this._net(this.props.match.params.page)
  }

  _net(page) {
    this.setState({ progressShow: true });
    const query = new AV.Query('Atricle');
    if (page === '') {
      query.equalTo('essence', 1)
    } else if (page) {
      query.contains('tag', page)
    }

    //const url = "http://localhost:8085/v1/qa_service/recommends/public/";
    const url = "http://49.234.73.158:8085/v1/qa_service/recommends/public";
    //const url = "http://49.234.73.158:8085/v1/user/login";

    let _this = this;

    let data;
    axios.get(url).then(function (response) {
      data = response.data;
      console.log(data);
      _this.setState({
        items: data,
        progressShow: false
      });
    }).catch(function (e) {
      alert(e);
    });

    query.find().then((items) => {

    }).catch((error) => {
      this._snackBarOpen('')
      this.setState({ progressShow: false })
    })
  }
  //  Dom
  render() {
    console.log("", this.state.items)
    const atricleItems = this.state.items.map((item, index) =>
        <AtricleItem key={item.id} history={this.props.history} item={item} MessageChildren={Message} />
    )
    return (
        <div>
          <Header history={this.props.history} />
          <div className="g-container home">
            <Progress show={this.state.progressShow} />
            <SnackBar open={this.state.snackBarOpen} content={this.state.content} />
            <div className="left">
              {atricleItems}
            </div>
            <div className="right">
              <div className="card">
                <br />
                <br />
              </div>
            </div>
          </div>
        </div>
    )
  }
  _snackBarOpen(content, time = 2000) {
    this.setState({ snackBarOpen: true, content: content });
    setTimeout(() => {
      this.setState({ snackBarOpen: false })
    }, time)
  }
  //  Props 
  componentWillReceiveProps(nextProps) {
    this._net(nextProps.match.params.page)
  }
  //  Props  State 
  shouldComponentUpdate(nextProps, nextState) {
    return true
  }
  // Dom 
  componentWillUpdate(nextProps, nextState) {

  }
  //  Dom 
  componentDidUpdate() {

  }
  // 
  componentWillUnmount() {

  }
}

export default Home


