import React, { Component } from 'react'
import Message from "../component/message"
import Header from "../component/header"
import $ from "jquery"
import "./userfocus.css"
import axios from "axios";
import cookie from "react-cookies";
import HistoryItem from "../component/historyItem";

class Userfocus extends Component {
  // 
  constructor(props, context) {
    super(props);
    this.state = { items: [] };
    this._click1=this._click1.bind(this)
    this._click2=this._click2.bind(this)
  }
  _click1(){
    this.props.history.push('./userhistory')
}
  _click2(){
    $("#22").click(function(){
      $("#1").hide();
      $("#3").hide();
      $("#2").show();
    });
  }
  // Dom 
  componentWillMount() {

  }

  //  Dom 
  componentDidMount() {
    this._net(this.props.match.params.page)
  }
  _net(page) {
    this.setState({ progressShow: true });
    const url = "http://localhost:8081/users/followList/"+cookie.load('userId');
    let _this = this;
    let data = [];
    axios.get(url).then(function (response) {
      //data = response.data;
      // console.log(response.data);
      // console.log("222222222222222222222222222222");

      for(let i = 0; i <response.data.length; i++) {

        const url = "http://localhost:8087/question/"+response.data[i];
        axios.get(url).then(function (response) {
          data.push(response.data);
          console.log(data);

          _this.setState({
            items: data,
            progressShow: false
          });

        }).catch(function (e) {
          alert(e);
        });
      }

    }).catch(function (e) {
      alert(e);
    });



  }
  //  Dom
  render() {
    let _this = this;
    console.log("444", this.state.items)
    const atricleItems = this.state.items.map((item, index) =>
        <HistoryItem key={item.id} history={this.props.history} item={item} MessageChildren={Message} />
    )
    return (
        <div>
          <Header history={this.props.history} />
          <div className="g-container userhistory">
            <div className="content">
                <button id="11"onClick={this._click1}style={{float:"left",marginLeft:"10px",width:"200px"}}></button>
                <button id="22"onClick={this._click2}style={{float:"left",marginLeft:"10px",outline:"none"}}></button>
              <div style={{marginLeft:"-50px",marginTop:"30px"}}>
              <div id="1" style={{display:"none"}}></div>
              <div id="2"  className="11111">{atricleItems}</div>
              </div>
            </div>
            <div className="right">
              <div className="card">
                <br />
                <br />
              </div>
            </div>
          </div>
        </div>
    )
  }
  _snackBarOpen(content, time = 2000) {
    this.setState({ snackBarOpen: true, content: content });
    setTimeout(() => {
      this.setState({ snackBarOpen: false })
    }, time)
  }
  //  Props 
  componentWillReceiveProps(nextProps) {
    this._net(nextProps.match.params.page)
  }
  //  Props  State 
  shouldComponentUpdate(nextProps, nextState) {
    return true
  }
  // Dom 
  componentWillUpdate(nextProps, nextState) {

  }
  //  Dom 
  componentDidUpdate() {

  }
  // 
  componentWillUnmount() {

  }
}

export default Userfocus



