import React, { Component } from 'react'
import { TextField, Button } from 'material-ui'
import AV from "leancloud-storage"
import Progress from "../component/progress"
import SnackBar from "../component/snackbar"
import { Redirect } from 'react-router-dom'
import "./login.css"
import axios from "axios";
import cookie from "react-cookies"

class Login extends Component {
  // 
  constructor(props, context) {
    super(props)
    this.state = {university:""}

    this._clickLogin = this._clickLogin.bind(this)
    this._onBlur = this._onBlur.bind(this)
    this._backHom = this._backHom.bind(this)
    this._RegisterClick=this._RegisterClick.bind(this)
    this._onChangeMail = this._onChangeMail.bind(this)
    this._onChangePassword = this._onChangePassword.bind(this)
  }
  // Dom 
  componentWillMount() {

  }
  //  Dom 
  componentDidMount() {

  }
  _RegisterClick(e){
    this.props.history.push("/login1");
  }
  // 
  _clickLogin(e) {
    this.setState({ progressShow: true });
    let _this = this;
    let data = new URLSearchParams();
    data.append('email',this.state.mail);
    data.append('password',this.state.password);
    const url = "http://localhost:8081/login";
    var code;
    axios.post(url, data)
        .then(function (response) {
          // handle success
          code = response.data;
          console.log(code);
          console.log(response);
        })
        .catch(function (error) {
          // handle error
          console.log(error);
        })
        .then(function () {
          console.log(code);
          switch (code.status) {
              //
            case false:alert("");break;
            case true:
              alert("");
              let date = new Date();
              var user=code.user;
              date.setTime(date.getTime() + (7 * 24 * 60 * 60 * 1000));
              console.log(user.university);
              cookie.save('userId', user.id, { expires: date ,path: '/'});
              cookie.save('university',user.university);
              console.log(cookie.loadAll());
              console.log(cookie.load('userId'));
              console.log(cookie.load('university'));
              console.log("`1`1112122222222");
              _this.props.history.push('./userhistory');
              break;
            default: alert("");break;
          }
        });
  }
  _onChangeMail(e) {
    this.setState({ mail: e.target.value })
    this._verify()
  }
  _onChangePassword(e) {
    this.setState({ password: e.target.value })
    this._verify()
  }
  // 
  _verify() {
    // 
    if (this.state.password && this.state.password.length >= 6
      && this.state.mail && this.state.mail.match(/^(.+)@(.+)$/)) {
      this.setState({ buttonLogin: true })
    } else {
      this.setState({ buttonLogin: false })
    }

    if (this.state.mail && this.state.mail.match(/^(.+)@(.+)$/)) {
      this.setState({ buttonMailError: false })
    } else if (this.state.mail) {
      this.setState({ buttonMailError: true })
      return
    }

    if (!this.state.password) return

    if (this.state.password && this.state.password.length >= 6) {
      this.setState({ buttonPasswordError: false })
    } else {
      this.setState({ buttonPasswordError: true })
    }

  }
  // 
  _onBlur(e) {
    this._verify()
  }
  // back 
  _backHom() {
    this.props.history.push('/')
  }
  //  Dom
  render() {
    const hiddenStyle = {
      display: 'none'
    }
    const visibleStyle = {
      display: ''
    }
    return (
      <div className="login">
        <Progress show={this.state.progressShow} />
        <SnackBar open={this.state.snackBarOpen} content={this.state.content} />
        <div className="box" style={this.state.findPsw ? hiddenStyle : visibleStyle}>
          <h1 onClick={this._backHom}>VSCodeChina</h1>
          <TextField
            required
            error={this.state.buttonMailError}
            className="item"
            label={this.state.buttonMailError ? '' : ''}
            onChange={this._onChangeMail}
            onBlur={this._onBlur}
          />
          <TextField
            required
            error={this.state.buttonPasswordError}
            className="item"
            label={this.state.buttonPasswordError ? ' 6 ' : ''}
            type="password"
            onChange={this._onChangePassword}
            onBlur={this._onBlur}
          />
          {/*  */}
          <div className="itemButton">
          <Button disabled={!this.state.buttonLogin} className={!this.state.buttonLogin ? 'button' : 'button blue'} onClick={this._clickLogin}>
              
          </Button>
          <Button className="button" onClick={this._RegisterClick}>
              
          </Button>
          </div>
        </div>
      </div>
    )
  }
  _snackBarOpen(content, time = 2000) {
    this.setState({ snackBarOpen: true, content: content })
    setTimeout(() => {
      this.setState({ snackBarOpen: false })
    }, time)
  }
  //  Props 
  componentWillReceiveProps(nextProps) {

  }
  //  Props  State 
  shouldComponentUpdate(nextProps, nextState) {
    return true
  }
  // Dom  
  componentWillUpdate(nextProps, nextState) {

  }
  //  Dom 
  componentDidUpdate() {

  }
  // 
  componentWillUnmount() {

  }
}

export default Login



