import React, { Component } from 'react'
import { Button, TextField, Dialog, DialogTitle, DialogContent, DialogActions, DialogContentText } from 'material-ui'
import AV from "leancloud-storage"
import Progress from "../component/progress"
import SnackBar from "../component/snackbar"
import Header from "../component/header"
import "./me.css"
import axios from 'axios'
import cookie from 'react-cookies'
import Mavatar from 'mavatar'
let avatar;

class Me extends Component {
  // 
  constructor(props, context) {
    super(props)
    this.state = {
      name: axios.get("http://localhost:8081/users/"+cookie.load('userId')).then(response => this.setState({name: response.data.name}))
          .catch(error => console.log("get data error")),
      age: axios.get("http://localhost:8081/users/"+cookie.load('userId')).then(response => this.setState({age: response.data.age}))
          .catch(error => console.log("get data error")),
      sex: axios.get("http://localhost:8081/users/"+cookie.load('userId')).then(response => this.setState({sex: response.data.sex}))
          .catch(error => console.log("get data error")),
      userstate:axios.get("http://localhost:8081/users/"+cookie.load('userId')).then(response => this.setState({userstate: response.data.signature}))
          .catch(error => console.log("get data error")),
    }
    this._clickSave = this._clickSave.bind(this)
    this._onChangeName = this._onChangeName.bind(this)
    this._onChangeAge = this._onChangeAge.bind(this)
    this._onChangeSex = this._onChangeSex.bind(this)
    this._onChangeUserstate = this._onChangeUserstate.bind(this)
    this._open = this._open.bind(this)
    this._close = this._close.bind(this)
    this._handleReset = this._handleReset.bind(this)
    this._handleClip = this._handleClip.bind(this)
    this._picture = this._picture.bind(this)
    this._clickJump = this._clickJump.bind(this)
  }
  // Dom 
  componentWillMount() {
  }
  //  Dom 
  componentDidMount() {
    avatar = new Mavatar({
      el: '#avatar',
      backgroundColor: '#ffffff'
    });
  }
  _handleClip = (e) => {
    avatar.imageClipper((dataurl) => {
      console.log(dataurl);
    });
  }
  _handleReset = (e) => {
    avatar.resetImage();
  }
  _picture(e){
    avatar.upload({
      url: 'http://localhost:8081/users/avatar',
      name: 'multipartFile',
      data: {id: cookie.load('userId')},
      success: function (data) {
        console.log(data);
        alert("")
      },
      error: function (error) {
        console.log(error)
      }
    });
  }
  _onChangeName(e) {
    this.setState({ name: e.target.value })

    if (e.target.value.length === 0) {
      this.setState({ nameError: true })
    } else {
      this.setState({ nameError: false })
    }
  }
  // 
  _clickSave(e) {
    if (this.state.name && (this.state.name.length === 0 || this.state.name.length > 20)) {
      this._snackBarOpen(' 20 ')
      return
    }
    // 
    for (let i = 0; i < this.state.name.length; i++) {
      const str = this.state.name[i];
      if (str === '@') {
        this._snackBarOpen(' @  ~_~')
        return
      }
      if (str === ' ') {
        this._snackBarOpen(' -_-')
        return
      }
    }

    this.setState({ progressShow: true })
    const url = "http://localhost:8081/users/information";
    var code;
    let data = new URLSearchParams();
    data.append('id',cookie.load('userId'));
    data.append('name',this.state.name);
    data.append('age',this.state.age);
    data.append('sex',this.state.sex);
    data.append('signature',this.state.userstate);
    console.log(this.state.name+ "");
    axios.put(url, data)
            .then(function (response) {
            // handle success
            code = response.data;
            console.log(code);
              console.log(response);
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          })
          .then(function () {
            console.log(code);
            switch (code) {
                //
              case false:
               alert("");break;
              case true:
                alert("");break;
            }
          });
  }
  // 
  // 
  // 
  _onChangeAge(e) {
    this.setState({ age: e.target.value })
  }
  // 
  _onChangeSex(e) {
    this.setState({ sex: e.target.value })
  }
  _onChangeUserstate(e) {
    this.setState({userstate:e.target.value})
  }
  _open(e) {
    this.setState({ show: true })
  }
  _close(e) {
    this.setState({ show: false })
  }
  _clickJump(e){
    this.props.history.push("/checkschool")
  }
  //  Dom
  render() {

    return (
        <div>
          <Header history={this.props.history} />
          <div className="g-container me">
            <Progress show={this.state.progressShow} />
            <SnackBar open={this.state.snackBarOpen} content={this.state.content} />
            <div className="content">
              <h3></h3>
              {/*  */}
              {/*  */}
              <div id="avatar">
                <button onClick={this._handleClip}></button>
                <button onClick={this._handleReset}></button>
                <button onClick={this._picture}></button>
              </div>
              {/*  */}
              <div className="cell">
                <TextField
                    required
                    error={this.state.nameError}
                    className="item"
                    value={this.state.name}
                    label={this.state.nameError ? '' : ''}
                    onChange={this._onChangeName}
                />
              </div>
              {/*  */}
              <div className="cell">
                <TextField
                    className="item"
                    value={this.state.age}
                    label={''}
                    onChange={this._onChangeAge}
                />
              </div>
              {/*  */}
              <div className="cell">
                <TextField
                    multiline
                    rowsMax="4"
                    className="item"
                    value={this.state.sex}
                    label={''}
                    onChange={this._onChangeSex}
                />
              </div>
              {/*  */}
              <div className="cell">
                <TextField
                    multiline
                    rowsMax="4"
                    className="item"
                    value={this.state.userstate}
                    label={''}
                    onChange={this._onChangeUserstate}
                />
              </div>
              <div className="cell">
                <Button className="button" onClick={this._clickSave}>
                  
                </Button>
                <Button className="button" onClick={this._clickJump}>
                  
                </Button>
              </div>
            </div>
            <div className="right">
              <div className="card">
                <br />
                <br />
              </div>
            </div>
          </div>
        </div>
    )
  }
  _snackBarOpen(content, time = 2000) {
    this.setState({ snackBarOpen: true, content: content })
    setTimeout(() => {
      this.setState({ snackBarOpen: false })
    }, time)
  }
  //  Props 
  componentWillReceiveProps(nextProps) {

  }
  //  Props  State 
  shouldComponentUpdate(nextProps, nextState) {
    return true
  }
  // Dom 
  componentWillUpdate(nextProps, nextState) {

  }
  //  Dom 
  componentDidUpdate() {

  }
  // 
  componentWillUnmount() {

  }
}

export default Me


