import React, { Component } from 'react'
import AV from "leancloud-storage"
import AtricleItem from "../component/atricleItem"
import Message from "../component/message"
import Progress from "../component/progress"
import SnackBar from "../component/snackbar"
import Header from "../component/header"
import "./checkschool.css"
import axios from "axios";
import {Button, TextField} from "material-ui";
import Mavatar from 'mavatar'
import cookie from "react-cookies";
import $ from "jquery"
let avatar1;


class Checkschool extends Component {
  // 
  constructor(props, context) {
    super(props);
    this.state = { items: [] }
    this._handleReset = this._handleReset.bind(this)
    this._handleClip = this._handleClip.bind(this)
    this._picture = this._picture.bind(this)
    this._getuniversity = this._getuniversity.bind(this)
 }

  // Dom 
  componentWillMount() {
  }

  //  Dom 
  componentDidMount() {
    avatar1 = new Mavatar({
      el: '#avatar1',
      backgroundColor: '#ffffff'
    });
  }
  _handleClip = (e) => {
    avatar1.imageClipper((dataurl) => {
      console.log(dataurl);
    });
  }
  _handleReset = (e) => {
    avatar1.resetImage();
  }
  _picture(e){
      let _this = this;
    avatar1.upload({
      url: 'http://localhost:8082/verification',
      name: 'multipartFile',
      data: {userId: cookie.load('userId'),intention:$("#selectbox").val(),remark:$("#inputbox").val()},
      success: function (data) {
        console.log(data+"123");
        alert("")
      },
      error: function (error) {
          console.log(_this.data+"123");
          console.log(error)
      }
    });
  }
  _getuniversity(e){
      const url = "http://localhost:8087/schools";
      let _this = this;
      let data;
      axios.get(url).then(function (response) {
          data= response.data;
          console.log(data);
          _this.setState({
              items: data,
          });
          console.log(data);
          for(var i=0;i<data.length;i++){
              $("#selectbox").append("<option value='"+data[i]+"'>"+data[i]+"</option>")
          }
      }).catch(function (e) {
          alert(e);
      });

  }

  //  Dom
  render() {
    return (
        <div>
          <Header history={this.props.history} />
          <div className="g-container me">
            <Progress show={this.state.progressShow} />
            <SnackBar open={this.state.snackBarOpen} content={this.state.content} />
            <div className="content">
              <h3></h3>
              <h4 style={{fontSize:"15px"}}></h4>
              <div id="avatar1" style={{marginTop:"10px"}}></div>
              <div>
                <Button className="button" onClick={this._handleClip} style={{marginTop:"10px"}}></Button>
                <Button className="button" onClick={this._handleReset} style={{marginTop:"10px"}}></Button>
                <Button className="button" onClick={this._picture} style={{marginTop:"10px"}}></Button>
              </div>
              <div>
                <select id="selectbox" style={{width:"100px"}}></select>
                <button onClick={this._getuniversity}></button>
              </div>
              <div>
                <input id="inputbox" placeholder=""/>
              </div>
            </div>
            <div className="right">
              <div className="card">
                <br />
                <br />
              </div>
            </div>
          </div>
        </div>
    )
  }
  _snackBarOpen(content, time = 2000) {
    this.setState({ snackBarOpen: true, content: content });
    setTimeout(() => {
      this.setState({ snackBarOpen: false })
    }, time)
  }
  //  Props 
  componentWillReceiveProps(nextProps) {
    this._net(nextProps.match.params.page)
  }
  //  Props  State 
  shouldComponentUpdate(nextProps, nextState) {
    return true
  }
  // Dom 
  componentWillUpdate(nextProps, nextState) {

  }
  //  Dom 
  componentDidUpdate() {

  }
  // 
  componentWillUnmount() {

  }
}

export default Checkschool



