import React from 'react'
import AtricleItem from "../component/atricleItem"
import Message from "../component/message"
import Progress from "../component/progress"
import SnackBar from "../component/snackbar"
import Header from "../component/header"
import axios from "axios";
import cookie from "react-cookies";
import HistoryItem from "../component/historyItem";
import NoButtonItem from "../component/noButtonItem";

class Searchpage extends React.Component {
    // 
    constructor(props, context) {
        super(props);
        const{searchId} = this.props.match.params
        console.log(searchId)
        this.state={
            items: []
        };
    }
    // Dom 
    componentWillMount() {
    }

    //  Dom 
    componentDidMount() {
        console.log("333333333333333333333333333333333333333333333");

        this._net(this.props.match.params.page)
    }
    _net(page) {
        const url = "http://localhost:8087/answers/regex/"+this.props.location.query.foo+"/"+cookie.load('university');
        console.log(cookie.load('university'));
        let _this = this;
        let data = [];
        axios.get(url).then(function (response) {
            data = response.data;
            console.log(data);
            console.log("323233333333333");
            _this.setState({
                items: data,
                progressShow: false
            });

        }).catch(function (e) {
            alert(e);
        });



    }

     //  Dom
    render()
    {    let _this = this;
        var header=this.props.location.query.foo;
        console.log(header);
        console.log("4544", this.state.items)
        const atricleItems = this.state.items.map((item, index) =>
            <NoButtonItem key={item.id} history={this.props.history} item={item} MessageChildren={Message} />
        )
        return (
            <div>
                <Header history={this.props.history} />
                <div className="g-container home">
                    <Progress show={this.state.progressShow} />
                    <SnackBar open={this.state.snackBarOpen} content={this.state.content} />
                    <div className="left">
                        {atricleItems}
                    </div>
                    <div className="right">
                        <div className="card">
                            <br />
                            <br />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    _snackBarOpen(content, time = 2000) {
        this.setState({ snackBarOpen: true, content: content });
        setTimeout(() => {
            this.setState({ snackBarOpen: false })
        }, time)
    }
    //  Props 
    componentWillReceiveProps(nextProps) {
    }
    //  Props  State 
    shouldComponentUpdate(nextProps, nextState) {
        return true
    }
    // Dom 
    componentWillUpdate(nextProps, nextState) {

    }
    //  Dom 
    componentDidUpdate() {

    }
    // 
    componentWillUnmount() {

    }
}

export default Searchpage


