import E from 'wangeditor'   // 
import React from "react";

class Editor extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            editor:null
        }
    }
    componentDidMount() {
        const elem = this.refs.editorElem; //editorElem
        const editor = new E(elem)  //new  editorElem
        editor.customConfig.uploadFileName = 'upfile' //
        editor.customConfig.uploadImgServer = ' '//'https://dev.xiaomon.com/api/treeroot/v1/xxx/upload/uploadImage'//
        editor.create() //
        editor.customConfig.uploadImgHooks = {
            customInsert: function (insertImg, result, editor) {
                var url = result.url  //
                insertImg(url)
            }
        }
        this.state.editor = editor;

    }
    render() {
        return (
            <div ref="editorElem" className="editorElem"></div>
        );
    }
}
export default Editor;


